.video-container {
  position: relative;
  overflow-y: scroll;
  // margin-top: 250px;
  height: 80vh;
}

.page-title {
  font-weight: bolder !important;
  color: #0097f5 !important;
  text-align: left;
  margin: 0;
  padding: 0;
  letter-spacing: -2px !important;
  font-family: $worksans-extrabold !important;
}

.video-title h2 {
  font-weight: bolder;
  color: #0097f5;
  font-family: $bold-sans;
}

.video-description h5 {
  line-height: 2;
  font-size: 12px;
}

.video-item h5 {
  font-weight: bolder;
  margin-top: 5px;
  color: black;
  line-height: 1.5;
  font-family: $bold-sans;
}

.short-desc {
  font-size: 12px;
}

.videoList-container {
  height: 80vh;
  overflow-x: hidden;
  // margin-top: 250px;
  overflow-y: scroll;
}

.videoList-container {
  .video-item {
    margin-bottom: 1.5em;

    &:hover {
      cursor: pointer;
    }
  }
}

.videoList-container > .video-item img {
  position: relative;
}

