@media only screen and (max-device-width: 780px) {
  .video-section {
    padding: 20px 0;
    .row {
      margin: auto;
      width: 100%;
    }
  }

  .video-section1 {
    padding: 20px 0;
    .row {
      margin: auto;
      width: 100%;
    }
  }

  .videoList-container {
    margin-top: 10px;
    width: 100%;

    .video-item {
      margin-bottom: 1.5em;

      &:hover {
        cursor: pointer;
      }
    }
  }

  // .searchpart {
  // 	top: 200px;
  // }

  .video-container {
    width: 100%;
    // margin-top: 50px;
    height: min-content;
  }

  .page-title {
    font-size: 20px;
  }

  video {
    height: 40vh;
  }

  .schoolLink {
    display: inline-block;
    padding: 10px 3px;
    word-spacing: -2px;
    font-size: 10px;
    font-weight: bold;
    text-transform: capitalize;
    color: rgba(7, 1, 1, 0.568);
  }
  .bold {
    background: transparent;
    color: #020202;
    font-family: $bold-sans;
    font-weight: bold;
  }
}
